<template>
<div>
 <div class="row">
   <div class="col">
        <b-form-group>
          Por página: &nbsp;
          <b-select size="sm" :options="perPageOptions" v-model="perPage" class="d-inline-block w-auto" />
        </b-form-group>
      </div>
      <div class="col">
        <b-form-group>
          <b-input size="sm"  placeholder="Procurar..." class="d-inline-block w-auto float-sm-right" v-model="search" @change="filter" />
        </b-form-group>
      </div>
    </div>
 <div class="table-responsive text-nowrap" >
        <b-table
        :items="filter"
        :responsive="true"
        :sort-icon-left="true"
        :fields="tableFields"
        :sticky-header="false"
        :striped="true"
        :hover="true"
        :bordered="true"
        :borderless="true"
        :outlined="true"
        :small="true"
        :selectable="true"
        :current-page="currentPage"
        :per-page="perPage" >
       <template slot="thead-top">
          <tr v-if="tableheader" >
            <th class="text-center" :colspan="item=='Agente' ? 1 : 3" v-for="(item,index) in tableheader" :key="index">{{item}}</th>
          </tr>
        </template>
        <template v-slot:custom-foot>
        <b-tr v-if="tablefooter">
          <b-td v-for="(item,key) in tablefooter" :key="key"><b>{{item}}</b></b-td></b-tr>
      </template>
       </b-table>
    </div>
     <div class="row">
      <div class="col-sm text-sm-left text-center mb-3 mb-sm-0">
        <span class="text-muted" v-if="totalPages">Página {{ currentPage }} de {{ totalPages }}</span>
      </div>
      <div class="col-sm">
        <b-pagination class="justify-content-center justify-content-sm-end"
          v-if="totalItems"
          v-model="currentPage"
          :total-rows="totalItems"
          :per-page="perPage"
          size="sm" />
      </div>
    </div>
    <export :hidden="!filename  " :table="tableData" :footer="tablefooter" :legend="legend" :header="header" :filename="filename" />
</div>
</template>
<script>
import Export from '@/components/Export'
export default {
  props: ['tableData', 'filename', 'tablefooter', 'header', 'legend', 'tableheader'],
  data: () => ({
    currentPage: 1,
    perPage: 10,
    perPageOptions: [10, 15, 20, 25, 30],
    search: '',
    totalTable: []
  }),
  components: {
    Export
  },
  computed: {
    tableFields () {
      if (!this.tableData[0]) return []
      return Object.keys(this.tableData[0]).map(key => ({
        key,
        sortable: this.isSortable
      }))
    },
    totalItems () {
      return this.tableData.length
    },
    totalPages () {
      return Math.floor(Math.ceil(this.totalItems / this.perPage)) || (this.totalItems ? 1 : 0)
    },
    filter () {
      const lista = this.tableData
      if (this.search) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.currentPage = 1
        return lista.filter(res => {
          res = Object.values(res)
          for (let k = 0; k < res.length; k++) {
            if (
              (res[k] + '').toLowerCase().indexOf(this.search.toLowerCase()) >=
              0
            ) {
              return true
            }
          }
          return false
        })
      }
      return lista
    }
  }
}
</script>
